
import { defineComponent } from 'vue';
import IconBack from './icons/IconBack.vue';
import IconClose from './icons/IconClose.vue';

export default defineComponent({
  name: 'Modal',
  components: { IconClose, IconBack },
  emits: ['back', 'close', 'next'],
  props: {
    isVisible: Boolean,
    isTypePage: Boolean,
    title: String,
    buttonText: String
  }
});
