
import { defineComponent } from 'vue';
import { debounce } from 'lodash';
import Modal from '@/components/Modal.vue';
import Review from '@/components/Review.vue';
import Toast from '@/components/Toast.vue';

export default defineComponent({
  name: 'Home',
  components: { Modal, Review, Toast },
  data: () => ({
    step: 1,
    amountOfSteps: 2,
    isTablet: false,
    isModalVisible: false,
    isSubmit: false,

    isToastVisible: false,
    wasSubmittedSuccessfuly: false
  }),
  watch: {
    isToastVisible() {
      setTimeout(() => {
        this.isToastVisible = false;
      }, 4000);
    }
  },
  computed: {
    modalButtonText(): string {
      if (!this.isTablet && this.step < this.amountOfSteps) {
        return 'Продолжить';
      } else {
        return 'Отправить';
      }
    },
    isModalTypePage(): boolean {
      if (!this.isTablet && this.step > 1) return true;
      return false;
    }
  },

  methods: {
    onWindowResize() {
      if (window.innerWidth >= 560) {
        this.isTablet = true;
      } else {
        this.isTablet = false;
      }
    },
    onModalNextClick() {
      if (!this.isTablet && this.step < this.amountOfSteps) {
        this.step += 1;
      } else {
        this.isSubmit = true;
        setTimeout(() => (this.isSubmit = false), 500);
      }
    },
    onModalBackClick() {
      if (this.step !== 1) {
        this.step -= 1;
      }
    },
    onReviewGoToStep(step: number) {
      this.step = step;
    },
    onReviewSubmitted(wasSuccessful = false) {
      if (wasSuccessful) {
        this.isModalVisible = false;
        this.step = 1;
      }

      this.isToastVisible = true;
      this.wasSubmittedSuccessfuly = wasSuccessful;
    }
  },

  mounted() {
    this.onWindowResize();
    window.addEventListener('resize', debounce(this.onWindowResize, 100));
  },

  beforeUnmount() {
    window.removeEventListener('resize', this.onWindowResize);
  }
});
